import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Button,
  Select,
  Input,
  Modal,
  message,
  Icon,
  DatePicker,
  Spin,
} from "antd";
import moment from "moment";

import "./index.css";
import { getAllEquipsService } from "../../../../services/equip";
import {
  addEprestimo,
  updateEprestimo,
  getEprestimoService,
  deleteEmprestimoService,
  CreatePDFEmprestimo,
} from "../../../../services/emprestimo";
import { getItens } from "../../../../services/produto";
import { getTecnico } from "../../../../services/tecnico";
import { validator, masks } from "./validator";

const { Option } = Select;
const { TextArea } = Input;

class EmprestimoContainer extends Component {
  state = {
    tecnicoArray: [],
    itemArray: [],
    disponiveis: [],
    reservados: [],
    avancado: true,
    loading: false,
    observacao: "",
    modalInClient: false,
    modalReservados: false,
    modalDisp: false,
    modalAdicionar: false,
    visible: false,
    page: 1,
    count: 1,
    show: 1,
    total: 10,
    retorno: {},
    atualizar: {},
    fieldFalha: {
      cnpj: false,
    },
    tecnico: "N√£o selecionado",
    razaoSocial: "",
    nomeProduto: "",
    productId: "",
    technicianId: "",
    serialNumber: "",
    emprestimoId: "",
    select: "disponiveis",
    search: false,
    fabricanteSearch: "",
    razaoSocialSearch: "",
    nomeProdutoSearch: "",
    serialNumberSearch: "",
  };

  onChangeTechnician = (value, props) => {
    this.setState({
      tecnico: value,
      technicianId: props.props.props.id,
    });
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleCancel = (e) => {
    this.setState({
      modalDisp: false,
      modalInClient: false,
      modalReservados: false,
      cnpj: "",
      razaoSocial: "",
      serialNumber: "",
      data: "",
      tecnico: "N√£o  selecionado",
      retorno: {},
      atualizar: {},
    });
  };

  getAllTecnico = async (name) => {
    const query = {
      filters: {
        technician: {
          specific: {
            name,
          },
        },
      },
    };

    await getTecnico(query).then((resposta) =>
      this.setState({
        tecnicoArray: resposta.data,
      })
    );
  };

  componentDidMount = async () => {
    await this.getAllItens();
    await this.getAllTecnico();
    await this.getAllEquips();
  };

  getEprestimo = async () => {
    await this.setState({
      loading: true,
    });
    const { select } = this.state;

    let dateExpedition = null;

    if (select === "emCliente") {
      dateExpedition = {
        start: "2019/01/01",
        end: new Date(),
      };
    } else {
      dateExpedition = {
        start: new Date(),
      };
    }

    const query = {
      filters: {
        emprestimo: {
          specific: {
            dateExpedition,
            razaoSocial: this.state.razaoSocialSearch,
          },
        },
        product: {
          specific: {
            name: this.state.nomeProdutoSearch,
            modulo: this.props.auth.modulo,
          },
        },
        equip: {
          specific: {
            serialNumber: this.state.serialNumberSearch,
          },
        },
      },
      page: this.state.page,
      total: this.state.total,
    };
    const { status, data } = await getEprestimoService(query);

    if (status === 200) {
      await this.setState({
        reservados: data.rows,
        page: data.page,
        count: data.count,
        show: data.show,
      });
    }
    await this.setState({
      loading: false,
    });
  };

  getAllEquips = async () => {
    await this.setState({
      loading: true,
    });

    const query = {
      filters: {
        equip: {
          specific: {
            loan: true,
            inClient: false,
            serialNumber: this.state.serialNumberSearch,
          },
        },
        product: {
          specific: {
            name: this.state.nomeProdutoSearch,
            modulo: this.props.auth.modulo,
          },
        },
        mark: {
          specific: {
            mark: this.state.fabricanteSearch,
          },
        },
      },
      page: this.state.page,
      total: this.state.total,
    };

    const { status, data } = await getAllEquipsService(query);

    if (status === 200) {
      this.setState({
        disponiveis: data.rows,
        page: data.page,
        count: data.count,
        show: data.show,
      });
    }
    this.setState({
      loading: false,
    });
  };

  changePages = async (pages) => {
    await this.setState({
      page: pages,
    });

    switch (this.state.select) {
      case "emCliente":
      case "reservados":
        await this.getEprestimo();
        break;
      case "disponiveis":
        await this.getAllEquips();
        break;
      default:
    }
  };

  getAllItens = async () => {
    const query = {
      filters: {
        product: {
          specific: {
            serial: true,
          },
        },
      },
    };

    await getItens(query).then((resposta) => {
      this.setState({
        itemArray: resposta.data,
      });
    });
  };

  onChangeItem = (value, product) => {
    this.setState({
      nomeProduto: value,
      productId: product.props.product.id,
    });
  };

  errorNumeroSerie = () => {
    message.error("Este equipamento ja foi registrado");
  };

  onChangeTecnico = async (value) => {
    await this.setState({
      tecnico: value,
    });
  };

  newEprestimo = async () => {
    const {
      razaoSocial,
      cnpj,
      data: dateExpedition,
      serialNumber,
      technicianId,
      observacao: observation,
    } = this.state;

    const value = {
      cnpj,
      razaoSocial,
      dateExpedition,
      serialNumber,
      technicianId,
      observation,
    };

    // eslint-disable-next-line no-unused-vars
    const { status, data } = await addEprestimo(value);

    if (status === 200) {
      this.setState({
        modalDisp: false,
        cnpj: "",
        razaoSocial: "",
        dateExpedition: "",
        serialNumber: "",
        technicianId: "",
        tecnico: undefined,
        data: undefined,
      });
      await this.getAllEquips();

      await this.getEprestimo();
    }
  };

  onChange = async (e) => {
    const { name, value } = masks(e.target.name, e.target.value);

    await this.setState({
      [name]: value,
    });

    if (name.indexOf("Search") !== -1) {
      switch (this.state.select) {
        case "emCliente":
        case "reservados":
          await this.setState({ page: 1 });
          await this.getEprestimo();
          break;
        case "disponiveis":
          await this.setState({ page: 1 });
          await this.getAllEquips();
          break;
        default:
      }
    }
  };

  onBlur = (e) => {
    const { fieldFalha } = validator(e.target.name, e.target.value, this.state);

    this.setState({
      fieldFalha,
    });
  };

  onFocus = (e) => {
    const { name } = e.target;
    const { fieldFalha } = this.state;

    fieldFalha[name] = false;

    this.setState({
      fieldFalha,
    });
  };

  onChangeData = (date) => {
    this.setState({
      data: date,
    });
  };

  disabledDate = (current) => {
    return current && current < moment().subtract(1, "day");
  };

  ModalDisponiveis = () => (
    <Modal
      title="Reservar equipamento"
      visible={this.state.modalDisp}
      onOk={this.newEprestimo}
      onCancel={this.handleCancel}
      okText="Salvar"
      cancelText="Cancelar"
      width={650}
    >
      <div className="div-rs1-Os">
        <div className="div-textRs-Os">Raz√£o social:</div>
        <div className="div-inputs">
          <Input
            className="input-100"
            style={{ width: "100%" }}
            name="razaoSocial"
            value={this.state.razaoSocial}
            placeholder="Digite a raz√£o social"
            onChange={this.onChange}
            onBlur={this.onBlur}
            onFocus={this.onFocus}
          />
        </div>
      </div>

      <div className="div-linha1-emprestimo">
        <div className="div-cnpj-imprestimo">
          <div className="div-text-Os">Cnpj:</div>
          <div className="div-inputs">
            <Input
              className={`input-100 ${
                this.state.fieldFalha.cnpj && "div-inputError"
              }`}
              style={{ width: "100%" }}
              name="cnpj"
              value={this.state.cnpj}
              placeholder="Digite o cnpj"
              onChange={this.onChange}
              onBlur={this.onBlur}
              onFocus={this.onFocus}
            />
          </div>
        </div>

        <div className="div-data1-emprestimo">
          <div className="div-textData-imprestimo">Data do atendimento:</div>
          <div className="div-inputs">
            <DatePicker
              className="input-100"
              onChange={this.onChangeData}
              name="data"
              format="DD/MM/YYYY"
              value={this.state.data}
              placeholder="Selecione uma data"
              disabledDate={this.disabledDate}
            />
          </div>
        </div>
      </div>

      <div className="div-linha1-emprestimo">
        <div className="div-produto-entrada">Observa√ß√£o: </div>
        <TextArea
          className="input-100"
          placeholder="Digite a observa√ß√£o"
          autosize={{ minRows: 2, maxRows: 4 }}
          rows={4}
          name="observacao"
          value={this.state.observacao}
          onChange={this.onChange}
        />
      </div>

      <div className="div-linha1-emprestimo">
        <div className="div-tecnico-emprestimo">
          <div className="div-text-Os">T√©cnico:</div>
          <div className="div-inputs">
            <Select
              className="input-100"
              defaultValue="N√£o selecionado"
              style={{ width: "100%" }}
              onChange={this.onChangeTechnician}
              showSearch
              onSearch={(name) => this.getAllTecnico(name)}
              placeholder="Nenhum tecnicos cadastrado"
              optionFilterProp="children"
              value={this.state.tecnico}
              name="technician"
              onFocus={this.onFocusTecnico}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
            >
              {this.state.tecnicoArray.map((valor) => (
                <Option props={valor} value={valor.name}>
                  {valor.name}
                </Option>
              ))}
            </Select>
          </div>
        </div>
      </div>
    </Modal>
  );

  deleteEmprestimo = async () => {
    const { id } = this.state.retorno;

    const { status, data } = await deleteEmprestimoService({ id });

    if (status === 200 && data === "sucesso") {
      await this.getAllEquips();

      await this.getEprestimo();
    }
    this.setState({
      modalInClient: false,
    });
  };

  ModalInClient = () => (
    <Modal
      title="Retorno ao estoque"
      visible={this.state.modalInClient}
      onOk={this.deleteEmprestimo}
      onCancel={this.handleCancel}
      okText="Retornar"
      cancelText="Cancelar"
    >
      <div className="div-rs1-Os">
        <div className="div-textRs-Os">Raz√£o social:</div>
        <div className="div-inputs">
          <Input
            readOnly
            className="input-100"
            style={{ width: "100%" }}
            name="razaoSocial"
            value={this.state.retorno.razaoSocial}
            onChange={this.onChange}
          />
        </div>
      </div>

      <div className="div-rs1-emprestimo">
        <div className="div-cnpj-emprestimo">
          <div className="div-text-Os">Cnpj:</div>
          <div className="div-inputs">
            <Input
              readOnly
              className="input-100"
              style={{ width: "100%" }}
              name="cnpj"
              value={this.state.retorno.cnpj}
              onChange={this.onChange}
            />
          </div>
        </div>

        <div className="div-dataSolici-emprestimo">
          <div className="div-textSolici-imprestimo">Data solicita√ß√£o:</div>
          <div className="div-inputs">
            <Input
              readOnly
              className="input-100"
              style={{ width: "100%" }}
              name="razaoSocial"
              value={this.state.retorno.createdAt}
              onChange={this.onChange}
            />
          </div>
        </div>
      </div>

      <div className="div-linha-emprestimo">
        <div className="div-data-emprestimo">
          <div className="div-textData1-emprestimo">Data atendimento:</div>
          <div className="div-inputs">
            <Input
              readOnly
              className="input-100"
              style={{ width: "100%" }}
              name="razaoSocial"
              value={this.state.retorno.dateExpedition}
              onChange={this.onChange}
            />
          </div>
        </div>
      </div>
    </Modal>
  );

  ModalConfirmDelet = () => (
    <Modal
      title="Atualizar reserva"
      visible={this.state.modalConfirmDelet}
      onOk={this.excluirEmprestimo}
      onCancel={() =>
        this.setState({ modalConfirmDelet: false, emprestimoId: "" })
      }
      okText="Confirmar"
      cancelText="Cancelar"
    >
      <h4>Aperte em "Confirmar" se deseja excluir a solicita√ß√£o</h4>
    </Modal>
  );

  ModalReservados = () => (
    <Modal
      title="Atualizar reserva"
      visible={this.state.modalReservados}
      onOk={this.handleUpdate}
      onCancel={this.handleCancel}
      okText="Retornar"
      cancelText="Cancelar"
      width={650}
    >
      <div className="div-rs1-Os">
        <div className="div-textRs-Os">Raz√£o social:</div>
        <div className="div-inputs">
          <Input
            readOnly
            className="input-100"
            style={{ width: "100%" }}
            name="razaoSocial"
            value={this.state.atualizar.razaoSocial}
            onChange={this.onChange}
          />
        </div>
      </div>

      <div className="div-rs1-emprestimo">
        <div className="div-data-emprestimo">
          <div className="div-textData1-emprestimo">Data solicita√ß√£o:</div>
          <div className="div-inputs" style={{ marginRight: "15px" }}>
            <Input
              readOnly
              className="input-100"
              style={{ width: "100%" }}
              name="razaoSocial"
              value={this.state.atualizar.createdAt}
              onChange={this.onChange}
            />
          </div>
        </div>

        <div className="div-data-emprestimo" style={{ width: "52%" }}>
          <div className="div-textData1-emprestimo">Data atendimento:</div>
          <div className="div-inputs">
            <DatePicker
              className="input-100"
              onChange={(data) =>
                this.setState({
                  atualizar: { ...this.state.atualizar, dateExpedition: data },
                })
              }
              format="DD/MM/YYYY"
              value={this.state.atualizar.dateExpedition}
              defaultValue={this.state.atualizar.dateExpedition}
              placeholder="Selecione uma data"
              disabledDate={this.disabledDate}
            />
          </div>
        </div>
      </div>

      <div className="div-linha1-emprestimo">
        <div className="div-produto-entrada">Observa√ß√£o: </div>
        <TextArea
          className="input-100"
          placeholder="Digite a observa√ß√£o"
          autosize={{ minRows: 2, maxRows: 4 }}
          rows={4}
          name="observation"
          value={this.state.atualizar.observation}
          onChange={this.onChange}
        />
      </div>

      <div className="div-linha-emprestimo">
        <div className="div-cnpj-emprestimo">
          <div className="div-text-Os">Cnpj:</div>
          <div className="div-inputs">
            <Input
              readOnly
              className="input-100"
              style={{ width: "100%" }}
              name="cnpj"
              value={this.state.atualizar.cnpj}
              onChange={this.onChange}
            />
          </div>
        </div>

        <div className="div-tecnico-update-emprestimo">
          <div>T√©cnico:</div>
          <div className="div-inputs">
            <Select
              className="input-100"
              defaultValue="N√£o selecionado"
              style={{ width: "100%" }}
              onChange={this.onChangeTechnician}
              showSearch
              onSearch={(name) => this.getAllTecnico(name)}
              placeholder="Nenhum tecnicos cadastrado"
              optionFilterProp="children"
              value={this.state.tecnico}
              name="technician"
              onFocus={this.onFocusTecnico}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
            >
              {this.state.tecnicoArray.map((valor) => (
                <Option props={valor} value={valor.name}>
                  {valor.name}
                </Option>
              ))}
            </Select>
          </div>
        </div>
      </div>
    </Modal>
  );

  testDisponivel = () => {
    if (this.state.disponiveis.length !== 0) {
      return this.state.disponiveis.map((item) => {
        return (
          <div className="div-100-Gentrada">
            <div className="div-lines-RPerda">
              <div className="cel-produto-cabecalho-emprestimo">
                {item.name}
              </div>
              <div className="cel-fabricante-cabecalho-estoque">
                {item.mark}
              </div>
              <div className="cel-numSerie-cabecalho-estoque">
                {item.serialNumber}
              </div>
              <div className="cel-acao-cabecalho-emprestimo">
                <Button
                  type="primary"
                  className="button"
                  onClick={() =>
                    this.setState({
                      modalDisp: true,
                      serialNumber: item.serialNumber,
                    })
                  }
                >
                  <Icon type="plus" />
                </Button>
              </div>
            </div>
            <div className=" div-separate1-Gentrada" />
          </div>
        );
      });
    } else {
      return (
        <div className="div-naotemnada">
          N√£o h√° nenhum empr√©stimo at√© o momento
        </div>
      );
    }
  };

  handleUpdate = async () => {
    const { atualizar, technicianId } = this.state;

    await updateEprestimo({
      ...atualizar,
      technicianId,
    });

    this.handleCancel();
    await this.getEprestimo();
  };

  excluirEmprestimo = async () => {
    const { emprestimoId: id } = this.state;

    this.setState({
      modalConfirmDelet: false,
      emprestimoId: "",
    });

    const value = { id, force: true };

    await deleteEmprestimoService(value);
    await this.getEprestimo();
    await this.getAllEquips();
  };

  update = async (item) => {
    const {
      razaoSocial,
      cnpj,
      createdAt,
      dateExpedition,
      id,
      observation,
      technician,
      technicianId,
    } = item;

    const { name, value } = masks("cnpj", cnpj);

    this.setState({
      modalReservados: true,
      atualizar: {
        razaoSocial,
        createdAt,
        dateExpedition: moment(dateExpedition.replace(/\D/gi, ""), "DDMMYYYY"),
        [name]: value,
        observation,
        id,
      },
      tecnico: technician,
      technicianId,
    });
  };

  tableReserved = () => {
    if (this.state.reservados.length !== 0) {
      return this.state.reservados.map((item) => {
        return (
          <div className="div-100-Gentrada">
            <div className="div-lines-RPerda">
              <div className="cel-produto-cabecalho-emprestimo">
                {item.product}
              </div>
              <div className="cel-razaosocial-cabecalho-emprestimo-reservados">
                {item.razaoSocial}
              </div>
              <div className="cel-numSerie-cabecalho-estoque">
                {item.serialNumber}
              </div>
              <div className="cel-acao-cabecalho-emprestimo-reservados">
                <Button
                  type="primary"
                  className="button"
                  onClick={() => this.update(item)}
                >
                  <Icon type="edit" />
                </Button>
                <Button
                  type="primary"
                  className="button-icon-remove"
                  onClick={() =>
                    this.setState({
                      modalConfirmDelet: true,
                      emprestimoId: item.id,
                    })
                  }
                >
                  <Icon type="delete" />
                </Button>
              </div>
            </div>
            <div className=" div-separate1-Gentrada" />
          </div>
        );
      });
    } else {
      return (
        <div className="div-naotemnada">
          N√£o h√° nenhum empr√©stimo at√© o momento
        </div>
      );
    }
  };

  tableInClient = () => {
    if (this.state.reservados.length !== 0) {
      return this.state.reservados.map((item) => {
        return (
          <div className="div-100-Gentrada">
            <div className="div-lines-RPerda">
              <div className="cel-produto-cabecalho-estoque">
                {item.product}
              </div>
              <div className="cel-razaosocial-cabecalho-emprestimo">
                {item.razaoSocial}
              </div>
              <div className="cel-numSerie-cabecalho-estoque">
                {item.serialNumber}
              </div>
              <div className="cel-acao-cabecalho-emprestimo">
                <Button
                  type="primary"
                  className="button"
                  onClick={() => this.retorno(item)}
                >
                  <Icon type="rollback" />
                </Button>
              </div>
            </div>
            <div className=" div-separate1-Gentrada" />
          </div>
        );
      });
    } else {
      return (
        <div className="div-naotemnada">
          N√£o h√° nenhum empr√©stimo at√© o momento
        </div>
      );
    }
  };

  Pages = () => (
    <div className="footer-Gentrada100-button">
      {Math.ceil(this.state.count / this.state.total) >= 5 &&
      Math.ceil(this.state.count / this.state.total) - this.state.page < 1 ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page - 4)}
        >
          {this.state.page - 4}
        </Button>
      ) : null}
      {Math.ceil(this.state.count / this.state.total) >= 4 &&
      Math.ceil(this.state.count / this.state.total) - this.state.page < 2 &&
      this.state.page > 3 ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page - 3)}
        >
          {this.state.page - 3}
        </Button>
      ) : null}
      {this.state.page >= 3 ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page - 2)}
        >
          {this.state.page - 2}
        </Button>
      ) : null}
      {this.state.page >= 2 ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page - 1)}
        >
          {this.state.page - 1}
        </Button>
      ) : null}
      <div className="div-teste">{this.state.page}</div>
      {this.state.page < this.state.count / this.state.total ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page + 1)}
        >
          {this.state.page + 1}
        </Button>
      ) : null}
      {this.state.page + 1 < this.state.count / this.state.total ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page + 2)}
        >
          {this.state.page + 2}
        </Button>
      ) : null}
      {this.state.page + 2 < this.state.count / this.state.total &&
      this.state.page < 3 ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page + 3)}
        >
          {this.state.page + 3}
        </Button>
      ) : null}
      {this.state.page + 3 < this.state.count / this.state.total &&
      this.state.page < 2 ? (
        <Button
          className="button"
          type="primary"
          onClick={() => this.changePages(this.state.page + 4)}
        >
          {this.state.page + 4}
        </Button>
      ) : null}
    </div>
  );

  onChangeSelect = async (value) => {
    await this.setState({
      select: value,
      loading: true,
      page: 1,
      count: 1,
      show: 1,
      total: 10,
      fabricanteSearch: "",
      razaoSocialSearch: "",
      nomeProdutoSearch: "",
      serialNumberSearch: "",
    });

    switch (value) {
      case "emCliente":
      case "reservados":
        await this.getEprestimo();
        break;
      case "disponiveis":
        await this.getAllEquips();
        break;
      default:
    }

    this.setState({
      loading: false,
    });
  };

  retorno = (item) => {
    const { razaoSocial, cnpj, createdAt, dateExpedition, id } = item;

    const { name, value } = masks("cnpj", cnpj);

    this.setState({
      modalInClient: true,
      retorno: {
        razaoSocial,
        createdAt,
        dateExpedition,
        [name]: value,
        id,
      },
    });
  };

  createPDFEmprestimo = async () => {
    let type = null;
    let dateExpedition = null;
    let rows = [];
    let query = {};

    switch (this.state.select) {
      case "disponiveis":
        type = "Disponiveis";
        break;
      case "reservados":
        type = "Reservados";
        dateExpedition = {
          start: new Date(),
        };
        break;
      case "emCliente":
        type = "Em Cliente";
        dateExpedition = {
          start: "2019/01/01",
          end: new Date(),
        };
        break;
      default:
        break;
    }

    switch (this.state.select) {
      case "emCliente":
      case "reservados":
        query = {
          filters: {
            emprestimo: {
              specific: {
                dateExpedition,
              },
            },
          },
          page: 1,
          total: null,
        };

        const { status, data } = await getEprestimoService(query);
        if (status === 200) {
          rows = data.rows;
        }
        break;
      case "disponiveis":
        query = {
          filters: {
            equip: {
              specific: {
                loan: true,
                inClient: false,
              },
            },
          },
          page: 1,
          total: null,
        };
        const response = await getAllEquipsService(query);

        if (response.status === 200) {
          rows = response.data.rows;
        }
        break;
      default:
        break;
    }

    await CreatePDFEmprestimo(type, rows);
  };

  Search = () => {
    const {
      fabricanteSearch,
      razaoSocialSearch,
      nomeProdutoSearch,
      serialNumberSearch,
    } = this.state;

    switch (this.state.select) {
      case "emCliente":
        return (
          <div className="div-linha-avancado-Rtecnico">
            <div className="div-linha1-avancado-Rtecnico">
              <div className="cel-produto-cabecalho-emprestimo-search">
                <Input
                  placeholder="Nome do produto"
                  name="nomeProdutoSearch"
                  style={{ width: "100%" }}
                  value={nomeProdutoSearch}
                  onChange={this.onChange}
                />
              </div>
              <div className="cel-razaosocial-cabecalho-emprestimo-reservados-search">
                <Input
                  placeholder="Raz√£o social"
                  name="razaoSocialSearch"
                  style={{ width: "100%" }}
                  value={razaoSocialSearch}
                  onChange={this.onChange}
                />
              </div>
              <div className="cel-numSerie-cabecalho-estoque-search">
                <Input
                  placeholder="N√∫mero de s√©rie"
                  name="serialNumberSearch"
                  style={{ width: "100%" }}
                  value={serialNumberSearch}
                  onChange={this.onChange}
                />
              </div>
            </div>
          </div>
        );
      case "reservados":
        return (
          <div className="div-linha-avancado-Rtecnico">
            <div className="div-linha1-avancado-Rtecnico">
              <div className="cel-produto-cabecalho-emprestimo-search">
                <Input
                  placeholder="Nome do produto"
                  name="nomeProdutoSearch"
                  style={{ width: "100%" }}
                  value={nomeProdutoSearch}
                  onChange={this.onChange}
                />
              </div>
              <div className="cel-razaosocial-cabecalho-emprestimo-reservados-search">
                <Input
                  placeholder="Raz√£o social"
                  name="razaoSocialSearch"
                  style={{ width: "100%" }}
                  value={razaoSocialSearch}
                  onChange={this.onChange}
                />
              </div>
              <div className="cel-numSerie-cabecalho-estoque-search">
                <Input
                  placeholder="N√∫mero de s√©rie"
                  name="serialNumberSearch"
                  style={{ width: "100%" }}
                  value={serialNumberSearch}
                  onChange={this.onChange}
                />
              </div>
            </div>
          </div>
        );
      case "disponiveis":
        return (
          <div className="div-linha-avancado-Rtecnico">
            <div className="div-linha1-avancado-Rtecnico">
              <div className="cel-produto-cabecalho-emprestimo-search">
                <Input
                  placeholder="Nome do produto"
                  name="nomeProdutoSearch"
                  style={{ width: "100%" }}
                  value={nomeProdutoSearch}
                  onChange={this.onChange}
                />
              </div>
              <div className="cel-fabricante-cabecalho-estoque-search">
                <Input
                  placeholder="Fabricante"
                  name="fabricanteSearch"
                  style={{ width: "100%" }}
                  value={fabricanteSearch}
                  onChange={this.onChange}
                />
              </div>
              <div className="cel-numSerie-cabecalho-estoque-search">
                <Input
                  placeholder="N√∫mero de s√©rie"
                  name="serialNumberSearch"
                  style={{ width: "100%" }}
                  value={serialNumberSearch}
                  onChange={this.onChange}
                />
              </div>
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  render() {
    return (
      <>
        <this.ModalDisponiveis />
        <this.ModalReservados />
        <this.ModalInClient />
        <this.ModalConfirmDelet />
        <div className="div-card-Gentrada">
          <div className="linhaTexto-Gentrada">
            <h1 className="h1-Gentrada">Gerenciar empr√©stimos</h1>
          </div>
          <div className="div-select-emprestimo">
            <Select
              value={this.state.select}
              style={{ width: "20%" }}
              onChange={this.onChangeSelect}
            >
              <Option value="disponiveis">DISPON√çVEIS</Option>
              <Option value="reservados">RESERVADOS</Option>
              <Option value="emCliente">EM CLIENTE</Option>
            </Select>

            <div style={{ display: "flex" }}>
              <Icon
                id="imprimir"
                style={{ fontSize: "32px", marginRight: "20px" }}
                onClick={this.createPDFEmprestimo}
                type="printer"
              />
              <Button
                type="primary"
                className="button"
                onClick={async () => {
                  await this.setState({
                    search: !this.state.search,
                    avancado: !this.state.avancado,
                  });

                  switch (this.state.select) {
                    case "emCliente":
                    case "reservados":
                      await this.getEprestimo();
                      break;
                    case "disponiveis":
                      await this.getAllEquips();
                      break;
                    default:
                  }
                }}
              >
                {this.state.avancado ? "Avan√ßado" : "Ocultar"}
              </Button>
            </div>
          </div>

          {this.state.search && <this.Search />}

          {this.state.select === "disponiveis" && (
            <div className="div-main-emprestimo">
              <div className="div-cabecalho-estoque">
                <div className="cel-produto-cabecalho-emprestimo">Produto</div>
                <div className="cel-fabricante-cabecalho-estoque">
                  Fabricante
                </div>
                <div className="cel-numSerie-cabecalho-estoque">Num. Serie</div>
                <div className="cel-acao-cabecalho-emprestimo" />
              </div>
              <div className=" div-separate-Gentrada" />
              {this.state.loading ? (
                <div className="spin">
                  <Spin spinning={this.state.loading} />
                </div>
              ) : (
                this.testDisponivel()
              )}
              <div className="footer-ROs">
                <this.Pages />
              </div>
            </div>
          )}
          {this.state.select === "reservados" && (
            <div className="div-main-emprestimo">
              <div className="div-cabecalho-estoque">
                <div className="cel-produto-cabecalho-emprestimo">Produto</div>
                <div className="cel-razaosocial-cabecalho-emprestimo-reservados">
                  Raz√£o social
                </div>
                <div className="cel-numSerie-cabecalho-estoque">Num. Serie</div>
                <div className="cel-acao-cabecalho-emprestimo-reservados" />
              </div>
              <div className=" div-separate-Gentrada" />
              {this.state.loading ? (
                <div className="spin">
                  <Spin spinning={this.state.loading} />
                </div>
              ) : (
                this.tableReserved()
              )}
              <div className="footer-ROs">
                <this.Pages />
              </div>
            </div>
          )}
          {this.state.select === "emCliente" && (
            <div className="div-main-emprestimo">
              <div className="div-cabecalho-estoque">
                <div className="cel-produto-cabecalho-estoque">Produto</div>
                <div className="cel-razaosocial-cabecalho-emprestimo">
                  Raz√£o social
                </div>
                <div className="cel-numSerie-cabecalho-estoque">Num. Serie</div>
                <div className="cel-acao-cabecalho-emprestimo" />
              </div>
              <div className=" div-separate-Gentrada" />
              {this.state.loading ? (
                <div className="spin">
                  <Spin spinning={this.state.loading} />
                </div>
              ) : (
                this.tableInClient()
              )}
              <div className="footer-ROs">
                <this.Pages />
              </div>
            </div>
          )}
        </div>
      </>
    );
  }
}

function mapStateToProps(state) {
  return {
    auth: state.auth,
  };
}

export default connect(mapStateToProps)(EmprestimoContainer);
